import one from '../../../static/img/positive3x.png'
import two from '../../../static/img/otherside3x.png'
import three from '../../../static/img/information3x.png'
import step from '../../../components/step/step'
import mButton from '../../../components/m-button.vue'
import tButton from '../../../components/twoButton/twoButton'
import ruiDatePicker from '../../../rattenking-dtpicker/rattenking-dtpicker.vue'
import wPicker from "@/components/w-picker/w-picker.vue";
import {
	formatDate
} from "../../../common/js/date.js";
export default {
	data() {
		return {
			step: {
				value1: '第一步',
				value2: '第二步',
				value3: '第三步',
				value4: '第四步',
				active: 'step-active',
				ago: 'step-ago',
				kong: '',
				kong1: '',
				kong2: ''
			},
			btn: {
				type: 'primary',
				value: '下一步',
				disabled: false,
				content: '上一步',
				commit: '提交',
				stepOne: true,
				stepTwo: false,
				stepThree: false,
				stepFour: false,
				Dates: false,
				dates: false
			},
			info: [
				"正面",
				"反面",

			],
			tellinfo: ["正面示例", "反面示例", "授权书示例"],
			num: 0,
			count: 0,
			src: one,

			ifday: false,
			ifdays: false,
			times: true,
			apply_one: true,
			idCardZ: '',
			idCardF: '',
			buyOil: '',
			takeOil: '',
			register: '',
			img: [],
			mode: 'date', //时间
			day: '2019-01-01', //购油有效期
			days: '2019-01-01', //提油有效期
			clientID:'',
		}
	},
	onLoad() {

		this.register = uni.getStorageSync('register');
		console.log(this.register.user)
		if (this.register.user == '购油人,提油人') {
			this.info.push(this.register.user.slice(0, 2))
			this.info.push(this.register.user.slice(4, 6))
			console.log(this.info)
		} else if (this.register.user == '购油人,发票领取人' || this.register.user == '提油人,发票领取人') {
			this.info.push(this.register.user.slice(0, 2))
			this.info.push(this.register.user.slice(4, 8))
			console.log(this.info)
		}
		console.log(this.info)
		this.getclientid();
	},
	methods: {
		getclientid(){
				const that = this;
				uni.getStorage({
					key:'clientid',
					success:function(res){
						that.clientID= res.data;
						console.log(res)
					}
				})
		},
		all() {
			if (this.btn.stepOne == true) {
				const that = this;
				uni.chooseImage({
					count: 1,
					success: function(res) {
						that.count = 1;
						console.log(res.tempFiles[0].path);
						that.idCardZ = res.tempFilePaths;
						that.src = res.tempFiles[0].path;
						// that.img.push({
						// 	name: 'id_fphoto',
						// 	uri: res.tempFilePaths[0]
						// })
						const photo = {
							name: 'id_fphoto',
							uri: res.tempFilePaths[0]
						}
						that.img.splice(0, 1, photo)
					}
				});
			} else if (this.btn.stepTwo == true) {
				const that = this;
				uni.chooseImage({
					count: 1,
					success: function(res) {
						console.log(res.tempFiles);
						that.count = 2;
						that.idCardF = res.tempFilePaths;
						that.src = res.tempFiles[0].path;
						// that.img.push({
						// 	name: 'id_bphoto',
						// 	uri: res.tempFilePaths[0]
						// })
						const photo = {
							name: 'id_bphoto',
							uri: res.tempFilePaths[0]
						}
						that.img.splice(1, 1, photo)
					}
				});
			} else if (this.btn.stepThree == true) {
				const that = this;
				if (this.register.user == '购油人,提油人' || this.register.user == '购油人,发票领取人') {
					uni.chooseImage({
						count: 1,
						success: function(res) {
							console.log(res.tempFiles);
							that.count = 3;
							that.buyOil = res.tempFilePaths;
							that.src = res.tempFiles[0].path;
							// that.img.push({
							// 	name: 'buy_auth_photo',
							// 	uri: res.tempFilePaths[0]
							// })
							const photo = {
								name: 'buy_auth_photo',
								uri: res.tempFilePaths[0]
							}
							that.img.splice(2, 1, photo)
						}
					});
				} else if (this.register.user == '提油人,发票领取人') {
					uni.chooseImage({
						count: 1,
						success: function(res) {
							console.log(res.tempFiles);
							that.count = 3;
							that.buyOil = res.tempFilePaths;
							that.src = res.tempFiles[0].path;
							// that.img.push({
							// 	name: 'get_auth_photo',
							// 	uri: res.tempFilePaths[0]
							// })
							const photo = {
								name: 'get_auth_photo',
								uri: res.tempFilePaths[0]
							}
							that.img.splice(2, 1, photo)
						}
					});
				}

			} else if (this.btn.stepFour == true) {
				const that = this;
				if (this.register.user == '购油人,提油人') {
					uni.chooseImage({
						count: 1,
						success: function(res) {
							console.log(res.tempFiles);
							that.count = 4;
							that.takeOil = res.tempFilePaths;
							that.src = res.tempFiles[0].path;
							// that.img.push({
							// 	name: 'get_auth_photo',
							// 	uri: res.tempFilePaths[0]
							// })
							const photo = {
								name: 'get_auth_photo',
								uri: res.tempFilePaths[0]
							}
							that.img.splice(3, 1, photo)
						}
					});
				} else {
					uni.chooseImage({
						count: 1,
						success: function(res) {
							console.log(res.tempFiles);
							that.count = 4;
							that.takeOil = res.tempFilePaths;
							that.src = res.tempFiles[0].path;
							// that.img.push({
							// 	name: 'bill_auth_photo',
							// 	uri: res.tempFilePaths[0]
							// })
							const photo = {
								name: 'bill_auth_photo',
								uri: res.tempFilePaths[0]
							}
							that.img.splice(3, 1, photo)
						}
					});
				}

			}

		},


		// 第一步下一步
		oneSide() {
			console.log(this.register.role)
			console.log(this.count)
			if (this.img.length == 1) {
				console.log(1)
				this.btn.stepOne = false;
				this.btn.stepTwo = true;
				this.num = 1;
				// this.count += 1;
				this.src = two;
				this.step.active = "step-ago",
					this.step.kong = "step-active"
			} else if (this.img.length > 1) {
				console.log(2)
				this.btn.stepOne = false;
				this.btn.stepTwo = true;
				this.num = 1;
				// this.count += 1;
				this.src = this.img[1].uri;
				this.step.active = "step-ago",
					this.step.kong = "step-active"
			} else {
				return uni.showToast({
					title: '请上传身份证正面照',
					icon: 'none'
				})
			}

		},
		//第二步上一步
		lastStep() {
			console.log(this.img[0].uri)
			this.src = this.img[0].uri
			this.btn.stepOne = true;
			this.btn.stepTwo = false;
			this.num = 0;
			// this.count -= 1;
			// this.src = one;
			this.step.active = "step-active";
			this.step.kong = ""
		},
		// 第二步下一步
		nextStep() {
			console.log(this.count)
			if (this.img.length == 2) {
				this.btn.stepTwo = false;
				this.btn.stepThree = true;
				this.btn.Dates = true;
				this.num = 2;
				// this.count += 1;
				this.src = three;
				this.step.active = "step-ago",
					this.step.kong = "step-ago"
				this.step.kong1 = "step-active"
			} else if (this.img.length > 2) {
				this.btn.stepTwo = false;
				this.btn.stepThree = true;
				this.btn.Dates = true;
				this.num = 2;
				// this.count += 1;
				this.src = this.img[2].uri;
				this.step.active = "step-ago",
					this.step.kong = "step-ago"
				this.step.kong1 = "step-active"
			} else {
				return uni.showToast({
					title: '请上传身份证反面照',
					icon: 'none'
				})
			}
		},
		//第三步上一步
		threeStepLast() {
			this.btn.stepThree = false;
			this.btn.stepTwo = true;
			this.btn.Dates = false;
			this.num = 1;
			// this.count -= 1;
			this.src = this.img[1].uri;
			this.step.active = "step-ago";
			this.step.kong = "step-active";
			this.step.kong1 = '';
		},
		// 第三步下一步
		threeStepNext() {
			console.log(this.register.role)
			console.log(this.count)
			if (this.img.length == 3) {
				if (this.ifday == true) {
					this.btn.stepThree = false;
					this.btn.stepFour = true;
					this.btn.Dates = false;
					this.btn.dates = true;
					this.num = 3;
					// this.count += 1;
					this.src = three;
					this.step.kong = "step-ago",
						this.step.kong1 = "step-ago"
					this.step.kong2 = "step-active"
				} else if (this.ifday == false) {
					return uni.showToast({
						title: '请选择授权有效期',
						icon: "none"
					})
				}

			} else if (this.img.length > 3) {
				this.btn.stepThree = false;
				this.btn.stepFour = true;
				this.btn.Dates = false;
				this.btn.dates = true;
				this.num = 3;
				// this.count += 1;
				this.src = this.img[3].uri;
				this.step.kong = "step-ago",
					this.step.kong1 = "step-ago"
				this.step.kong2 = "step-active"
			} else {
				return uni.showToast({
					title: '请上传购油授权书',
					icon: 'none'
				})
			}

		},
		fourStepLast() {
			this.btn.stepFour = false;
			this.btn.stepThree = true;
			this.btn.Dates = true;
			this.btn.dates = false;
			this.num = 2;
			// this.count -= 1;
			this.src = this.img[2].uri;
			this.step.kong = "step-ago",
				this.step.kong1 = "step-active",
				this.step.kong2 = ''
		},
		fourStepNext() {
			const imgs = this.img.map((value, index) => {
				return {
					name: value.name,
					uri: value.uri
				}
			})

			if (this.img.length >= 4) {
				console.log(imgs)
				if (this.ifdays == true) {
					if (this.register.user == '购油人,提油人') {
						uni.uploadFile({
							url: 'http://dev.pjy.name:8180/api/bizcust/base/regist',
							//url: 'http://192.168.0.156:8080/api/bizcust/base/regist',
							files: imgs,
							formData: {
								"username": this.register.userPhoneNum,
								"passwd": this.register.newPwd2,
								"roles": this.register.role,
								"customer_id": this.register.companyId,
								"manager_id": this.register.customerId,
								"realname": this.register.userName,
								"id_card": this.register.userId,
								"phone": this.register.userPhoneNum,
								"city": this.register.userCity,
								"buy_auth_exp": this.day,
								"get_auth_exp": this.days,
								"client_id" :this.clientID,
							},
							success: function(res) {
								var data = JSON.parse(res.data)
								//var data = res.data
								console.log(data)
								if (res.statusCode == 200) {
									if (data.errorCode == 0) {
										uni.showToast({
											"title": '注册已提交，审核成功后即可登录使用',
											icon: 'none'
										})
										uni.removeStorage({
											key: 'register'
										})
										uni.navigateTo({
											url: '../../login/login?val=' + data.value,
										});

									} else {
										uni.showToast({
											"title": data.message,
											"icon": 'none'
										});

									}
								}
							}
						})
					} else if (this.register.user == '购油人,发票领取人') {
						uni.uploadFile({
							url: 'http://dev.pjy.name:8180/api/bizcust/base/regist',
							//url: 'http://192.168.0.156:8080/api/bizcust/base/regist',
							files: imgs,
							formData: {
								"username": this.register.userPhoneNum,
								"passwd": this.register.newPwd2,
								"roles": this.register.role,
								"customer_id": this.register.companyId,
								"manager_id": this.register.customerId,
								"realname": this.register.userName,
								"id_card": this.register.userId,
								"phone": this.register.userPhoneNum,
								"city": this.register.userCity,
								"buy_auth_exp": this.day,
								"bill_auth_exp": this.days,
								"client_id" :this.clientID,
							},
							success: function(res) {
								var data = JSON.parse(res.data)
								// var data = res.data
								console.log(data)
								if (res.statusCode == 200) {
									if (data.errorCode == 0) {
										uni.showToast({
											"title": '注册已提交，审核成功后即可登录使用',
											icon: 'none'
										})
										uni.removeStorage({
											key: 'register'
										})
										uni.navigateTo({
											url: '../../login/login?val=' + data.value,
										});

									} else {
										uni.showToast({
											"title": data.message,
											"icon": 'none'
										});

									}
								}
							}
						})
					} else {
						uni.uploadFile({
							url: 'http://dev.pjy.name:8180/api/bizcust/base/regist',
							//url: 'http://192.168.0.156:8080/api/bizcust/base/regist',
							files: imgs,
							formData: {
								"username": this.register.userPhoneNum,
								"passwd": this.register.newPwd2,
								"roles": this.register.role,
								"customer_id": this.register.companyId,
								"manager_id": this.register.customerId,
								"realname": this.register.userName,
								"id_card": this.register.userId,
								"phone": this.register.userPhoneNum,
								"city": this.register.userCity,
								"get_auth_exp": this.day,
								"bill_auth_exp": this.days,
								"client_id" :this.clientID,
							},
							success: function(res) {
								var data = JSON.parse(res.data)
								//var data = res.data
								console.log(res)
								if (res.statusCode == 200) {
									if (data.errorCode == 0) {
										uni.showToast({
											"title": '注册已提交，审核成功后即可登录使用',
											icon: 'none',
										})
										uni.removeStorage({
											key: 'register'
										})
										uni.navigateTo({
											url: '../../login/login?val=' + data.value,
										});

									} else {
										uni.showToast({
											"title": data.message,
											"icon": 'none'
										});

									}
								}
							}
						})
					}

				} else if (this.ifdays == false) {
					return uni.showToast({
						title: '请选择授权有效期',
						icon: 'none'
					})
				}
			} else {
				return uni.showToast({
					title: '请上传提油授权书',
					icon: 'none'
				})
			}
		},
		// 选择时间
		changeTimes() {
			this.times = false;
			this.$refs.date.show();
		},
		changeTime() {
			this.apply_one = false;
			this.$refs.date1.show();
		},
		onConfirm(val) {
			console.log(val)
			this.day = val.result;
			this.ifday = true
		},
		onConfirms(val) {
			this.days = val.result;
			this.ifdays = true
		},

	},
	components: {
		step,
		mButton,
		tButton,
		ruiDatePicker,
		wPicker
	},
}
